.first {
  position: relative;
  width: 100%;
  height: 100%;
  color: #fff;
}
.firstBgBox {
  width: 100%;
  height: 100%;
  background: url(/static/img/header-background.da34354.jpg) !important;
  background-size: cover;
  background-position-x: 50%;
}
.firstBg {
  background-size: cover;
  -webkit-transition-duration: .5s;
          transition-duration: .5s;
  -webkit-transform: translateY(-25%);
          transform: translateY(-25%);
  opacity: .5;
}
.first .title {
  position: absolute;
  top: 43%;
  left: 0;
  right: 0;
}
.first .title h1 {
  font-size: 60px;
}
.first .title .subline {
  font-size: 30px;
  padding-top: 20px;
